//微软雅黑字体
@FONT-YAHEI: \5fae\8f6f\96c5\9ed1;

.demo-pagetitle {
  font-size: .68rem; /* px */
  color: #FF5E53;
  text-align: center;
  padding: .5rem 0;
}

.demo-detail-title {
  color: #888;
  font-size: .28rem; /* px */
  margin-bottom: .8rem;
  font-weight: normal;
  line-height: 0.42rem;
  text-align: center;
}

.demo-loading {
  margin-bottom: 20px; /* no */
  li {
    a {
      align-items: center;
      justify-content: center;
      display: flex;
      overflow: hidden;
      width: 100%;
      height: 100%;
    }
    img {
      width: .6rem;
      height: .6rem;
      display: inline-block;
    }
  }
}

.demo-tip {
  background-color: #EFEFEF;
  color: #FF756B;
  font-size: .24rem; /* px */
  padding: .2rem .24rem;
  line-height: 20px; /* no */
  font-family: @FONT-YAHEI;
  i {
    margin-right: 3px; /* no */
  }
  a {
    color: #5AA700;
  }
}

.demo-upload {
  overflow: hidden;
  li {
    width: 1rem;
    height: 1rem;
    float: left;
    margin-right: 0.2rem;
    &:last-child {
      margin-right: 0;
    }
  }
}

.demo-upload-big {
  width: 2rem;
  height: 2rem;
  margin-top: 0.4rem;
}

.demo-upload-rectangle {
  width: 4rem;
  height: 2rem;
  margin-top: 0.4rem;
}

.demo-components {
  background-color: #FFF;
  padding: 10px; /* no */
  margin-bottom: 10px; /* no */
}

.demo-upload {
  overflow: hidden;
  li {
    float: left;
    img {
      width: 1rem;
      height: 1rem;
      margin-right: .2rem;
    }
  }
}

.demo-pitch {
  margin-top: .5rem;
}

.demo-small-pitch {
  margin-top: .2rem;
}

.demo-badege {
  padding: 0 .24rem;
}

.demo-icons {
  [class^="icon-"]:before, [class*=" icon-"]:before {
    font-size: .6rem; /* px */
    color: #FF685D;
  }
}

@demo-icons: 'demo-icons';
@font-face {
  font-family: @demo-icons;
  src: url('http://at.alicdn.com/t/font_1468809406_382368.ttf') format('truetype');
}

